import React from 'react'
import { graphql } from 'gatsby'

import Layout from '../components/layout'

import {
  Window,
  TitleBar,
  Button,
  ButtonRed,
  ButtonYellow,
  ButtonGreen,
  Content,
  Title,
  SubTitle,
} from '../styling/page.style'

const Project = ({ data }) => {
  return data.allPagesJson.edges.map(({ node, index }) => {
    return (
      <Layout key={`${index}-project`}>
        <h2>{node.title}</h2>
        <p>{node.summary}</p>
        <Window>
          <TitleBar>
            <Button>
              <ButtonRed />
              <ButtonYellow />
              <ButtonGreen />
            </Button>
          </TitleBar>
          <Content>
            <div>
              <Title style={{ fontSize: '1.3rem', color: '#fff' }}>
                /* --What I have done-- */
              </Title>
              {node.projects.map((item, index) => {
                return (
                  <div key={`${index}-item`}>
                    <SubTitle>/* {item.project_title} */</SubTitle>
                    <div>Built for: {item.description}</div>
                    <div>Built with: {item.tools}</div>
                  </div>
                )
              })}
            </div>
          </Content>
        </Window>
      </Layout>
    )
  })
}

export default Project

export const query = graphql`
  query {
    allPagesJson(filter: { path: { eq: "project" } }) {
      edges {
        node {
          path
          title
          summary
          projects {
            project_title
            link_project
            description
            tools
          }
        }
      }
    }
  }
`
